<script setup>
	// 导入 Composition API 相关函数
	import {
		ref
	} from 'vue'
	import NavigationBar from '../../components/navigation-bar.vue';

	// 定义响应式数据
	const current = ref("my")

	// 跳转到首页
	const gotoHome = () => {
		uni.showLoading({
			title: "加载中..."
		})

		uni.switchTab({
			url: "/pages/index/index",
			success: () => {
				uni.hideLoading()
			},
			fail: (err) => {
				uni.hideLoading()
				console.error("页面跳转失败", err)
				uni.showToast({
					title: "跳转失败",
					icon: "none"
				})
			}
		})
	}

	// 关于我们页面跳转
	const bindAbout = () => {
		uni.showLoading({
			title: "加载中..."
		})

		uni.navigateTo({
			url: "/mypackage/pages/about/about",
			success: () => {
				uni.hideLoading()
			},
			fail: (err) => {
				uni.hideLoading()
				console.error("页面跳转失败", err)
				uni.showToast({
					title: "跳转失败",
					icon: "none"
				})
			}
		})
	}
</script>

